.auth-page{
  background: #02aab0; /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #02aab0, #00cdac); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #02aab0, #00cdac); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  min-height: calc(100vh);
}
.auth-box{
  text-align: center;
  margin: auto;
}
.auth-form-box{
  margin: auto;
  padding: 20px;
}
.auth-form-box h1{
  color: white;
}
.auth-form-contain{
  margin-top: 40px;
  margin-bottom: 5px;
}
.auth-form-item{
  margin-top: 10px;
  margin-bottom: 10px;
}
.auth-form-item input {
  border: none;
  border-bottom-style: solid;
  border-bottom-width: thin;
  border-bottom-color: white;
  padding: 5px;
  background: transparent;
}
.auth-button{
  margin-top: 40px;
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 30px;
  border-style: solid;
  border-width: thin;
  border-color: white;
  background-color: transparent;
}
.auth-button:hover{
  background-color: white;
  color: #00cdac;
}
.password-forgotten{
  font-size: 0.9em;
  color: #FFFFFF8C;
}
