.nomatch-page{
  background: #02aab0; /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #02aab0, #00cdac); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #02aab0, #00cdac); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  min-height: calc(100vh);
}
.nomatch-box{
  margin-top: 40px;
}
.nomatch-box h1{
  font-size: 12em;
  margin-top: 10%;
}
.nomatch-box h2, h1, div{
  color: white;
  text-align: center;
}
.nomatch-box h2{
  margin-top: 40px;
}
.nomatch-box div{
  text-decoration: underline;
}
.nomatch-box div:hover{
  color: #FFFFFF8C;
}
