  border-bottom-color: #FFFFFF8C;
  width: 100%;
}

.Project-title-display:focus {
  border-color: black;
  background-color: black;
}

/*  Scene bar style */
.Scene-bar {
  max-height: 600px;
  padding-top: 2px;
}
